export default {
  transition: 'data',
  middleware: ['auth-admin'],
  data() {
    return {
      heroAlt: [
        {
          src: '',
          heading: ' About Us ',
          icon: 'mdi-account',
        },
      ],
      tab: 'products',
      id: '',
      filteredList: [],
      blogList: [],
      dialog: false,
      categories: ['Fibre Blowing', 'Klein Tools', 'Utilities', 'Telecoms'],
      headings: [
        'Name',
        'ProductId',
        'Enquiries',
        'Main Image',
        'Category',
        'Edit',
        'Delete',
      ],
      blogHeadings: ['Title', 'Date', 'Categories', 'Edit', 'Delete'],
      items: [{}],
      product: null,
      blog: null,
      element: '',
      isMobile: false,
    }
  },
  created() {
    this.getAllProducts()
    this.getAllBlogs()
  },
  methods: {
    async addGroup() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const { data } = await this.$axios.post(
          'https://cbsbackend.herokuapp.com/api/products/',
          this.$auth.user,
          config
        )

        this.getAllProducts()
        return data
      } catch (err) {
        throw new Error('Error adding product')
      }
    },

    showGroup(index) {
      this.product = index
    },
    onResize() {
      if (window.innerWidth < 969) {
        this.isMobile = true
      } else {
        this.isMobile = false
      }

      if (this.isMobile) {
        this.headings = ['Name', 'Main Image', 'Edit', 'Delete']
        this.blogHeadings = ['Title', 'Edit', 'Delete']
      }
    },
    async deleteGroup() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        await this.$axios.delete(
          `https://cbsbackend.herokuapp.com/api/products/${
            this.filteredList[this.product]._id
          }`,
          config
        )
      } catch (err) {
        throw new Error('Error deleting Product')
      }

      this.dialog = false

      this.getAllProducts()
    },
    showBlogGroup(index) {
      this.blog = index
    },
    async deleteBlogGroup() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        await this.$axios.delete(
          `https://cbsbackend.herokuapp.com/api/blogs/${
            this.blogList[this.blog]._id
          }`,
          config
        )
      } catch (err) {
        throw new Error('Error deleting Product')
      }

      this.dialog = false

      this.getAllBlogs()
    },
    async addBlogGroup() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const { data } = await this.$axios.post(
          'https://cbsbackend.herokuapp.com/api/blogs/',
          this.$auth.user,
          config
        )

        this.getAllBlogs()
        return data
      } catch (err) {
        throw new Error('Error adding product')
      }
    },
    async getAllProducts() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const response = await this.$axios.get(
          'https://cbsbackend.herokuapp.com/api/products',
          config
        )
        this.filteredList = response.data
      } catch (err) {
        throw new Error('Error Fetching Products')
      }
    },
    async getAllBlogs() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const response = await this.$axios.get(
          'https://cbsbackend.herokuapp.com/api/blogs',
          config
        )
        this.blogList = response.data
      } catch (err) {
        throw new Error('Error Fetching Blogs')
      }
    },
  },

  head() {
    return {
      title: 'Products',
      meta: [
        {
          hid: 'description',
          name: 'description',
          content:
            'Infographic hypotheses influencer user experience Long madel ture gen-z paradigm shift client partner network product seilans solve management influencer analytics leverage virality. incubator seed round massmarket. buyer agile development growth hacking business-to-consumer ecosystem',
        },
      ],
    }
  },
}
